import styled from "@emotion/styled";

export const ButtonStyled = styled.button`
  width: 150px;
  height: 36px;
  background-color: #1565c0;
  color: #fff;
  border-radius: 4px;
  border: none;
  text-transform: uppercase;
  cursor:pointer;
  transition: 0.4s;
  :hover{
    background-color: #32bdf8;
  }
`